.checkbox, .radio, .switch, .legend {
  display: inline-flex;
  margin: 0;
  position: relative;

  &.checked .wrapper {
    color: #697582;
  }

  &:hover {
    input[type="checkbox"]:not(:disabled), input[type="radio"]:not(:disabled) {
      &::before {
        background-color: #697582;
        transition: all 85ms cubic-bezier(0.2, 0.2, 0.38, 0.9) 0s;
      }

      &:checked::before {
        background-color: #00ADEF;
        transition: all 85ms cubic-bezier(0.2, 0.2, 0.38, 0.9) 0s;
      }
    }
  }

  .wrapper {
    display: inline-block;
    height: 40px;
    width: 40px;
    overflow: hidden;
    position: relative;
    border-radius: 50%;
    color: #00ADEF;

    input[type="checkbox"],
    input[type="radio"] {
      height: 100%;
      width: 100%;
      margin-left: -100%;

      &::before,
      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        visibility: visible;
        border-radius: 50%;
        transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, left 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        opacity: 1;
        left: 0;
      }

      &::before {
        opacity: 0.08;
      }

      &::after {
        left: 10px;
        top: 10px;
        width: 20px;
        height: 20px;
        border: 1px solid #c0c8d0;
        border-radius: 1px;
      }

      &:checked::after {
        background: url("data:image/png;base64,")
          no-repeat;
        background-position: center;
        background-size: 12px;
      }

      &:disabled {
        &::after {
          opacity: 0.8;
        }

        &:hover::before {
          background-color: transparent;
        }
      }
    }

    input[type="radio"] {
      &::before,
      &::after {
        border-radius: 50%;
      }

      &::after {
        left: 12px;
        top: 12px;
        width: 16px;
        height: 16px;
      }

      &:checked::after {
        background: url("data:image/png;base64,")
          no-repeat;
        background-position: center;
        background-size: 10px;
      }
    }
  }

  .label {
    line-height: 40px;
    font-size: 14px;
    color: #383f49;
  }

  &.disabled .label {
    color: #99a5b3;
  }
}

.switch {
  cursor: pointer;

  &.on .wrapper {
    color: #697582;

    &::before {
      background-color: #00ADEF;
    }
  }

  .wrapper {
    overflow: visible;
    width: 48px;
    
    input[type="checkbox"], input[type="radio"] {
      cursor: pointer;
      margin-left: 0;
      outline: none;
      -webkit-appearance: none;

      &::before {
        z-index: 3;
        left: -12px;
        width: 40px;
      }

      &:hover::before {
        background-color: #697582;
      }

      &::after {
        border: none;
        background: none;
        background-color: #ffffff;
        width: 14px;
        height: 14px;
        top:13px;
        left: 2px;
        border-radius: 50%;
        z-index: 4;
      }

      &:checked::after {
        background: none;
        left: 19px;
        background-color: #ffffff;
      }

      &:checked::before {
        left: 7px;
      }

      &:checked + .animation-wrapper {
        left: 6px;
        transition: left 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
      }
    }

    &::before {
      content: "";
      width: 35px;
      height: 18px;
      background-color: #C0C8D0;
      border-radius: 13px;
      position: absolute;
      top:11px;
      z-index: 1;
      transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    }

    .animation-wrapper {
      position: absolute;
      display: block;
      width: 40px;
      height: 40px;
      z-index: 2;
      border-radius: 50%;
      top:0;
      left: -12px;
      transition: left 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    }
  }
}

.legend {
  .label {
    color: #99a5b3;
    font-size: 12px;
    line-height: 33px;
  }

  &.active .label {
    color: #383f49;
  }

  .wrapper {
    width: 33px;
    height: 33px;
    input[type="checkbox"],
    input[type="radio"] {
      &::after {
        border: none;
        background: none;
        background-color: #d9dfe4;
        width: 13px;
        height: 13px;
        top: 10px;
        left: 10px;
        border-radius: 1px;
      }

      &:checked::after {
        background: none;
        background-color: #00adef;
      }
    }
  }
}