@font-face {
  font-family: "Roboto-Regular";
  src: url(./fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: "Roboto-Medium";
  src: url(./fonts/Roboto-Medium.ttf);
}

@font-face {
  font-family: "icomoon";
  src: url(./fonts/icomoon.ttf);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

main {
  display: block;
}

body {
  margin: 0;
  font-family: "Roboto-Regular", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important;
}

hr {
  margin: 15px 0;
  color: inherit;
  background-color: currentColor;
  border: 0;
  opacity: 0.25;
}

hr:not([size]) {
  height: 1px;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 7.5px;
  font-weight: 500;
  line-height: 1.2;
}

h1 {
  font-size: 2.34375rem;
}

@media (max-width: 1200px) {
  h1 {
    font-size: calc(1.359375rem + 1.3125vw) ;
  }
}

h2 {
  font-size: 1.875rem;
}

@media (max-width: 1200px) {
  h2 {
    font-size: calc(1.3125rem + 0.75vw) ;
  }
}

h3 {
  font-size: 1.640625rem;
}

@media (max-width: 1200px) {
  h3 {
    font-size: calc(1.289062rem + 0.46875vw) ;
  }
}

h4 {
  font-size: 1.40625rem;
}

@media (max-width: 1200px) {
  h4 {
    font-size: calc(1.265625rem + 0.1875vw) ;
  }
}

h5 {
  font-size: 1.171875rem;
}

h6 {
  font-size: 0.9375rem;
}

p {
  margin-top: 0;
  margin-bottom: 15px;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]), a:not([href]):not([tabindex]):hover, a:not([href]):not([tabindex]):focus {
  color: inherit;
  text-decoration: none;
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #6c757d;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button:not(:disabled),
[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled) {
  cursor: pointer;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

@media (max-width: 1200px) {
  legend {
    font-size: calc(1.275rem + 0.3vw) ;
  }
}

mark {
  padding: 0.2em;
  background-color: #fcf8e3;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

@font-face {
  font-family: 'Icons';
  src: url("./fonts/icomoon.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
}

i.icon {
  display: inline-block;
  opacity: 1;
  margin: 0em 0.25rem 0em 0em;
  width: 1.18em;
  height: 1em;
  font-family: 'Icons';
  font-style: normal;
  font-weight: normal;
  text-decoration: inherit;
  text-align: center;
  speak: none;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

i.icon::before {
  background: none !important;
}

i.icon.arrow {
  margin-top: 0.25em;
}

i.icon.arrow::before {
  content: "\e901";
}

i.icon.arrow.up {
  transform: rotate(90deg);
  font-weight: bold;
}

i.icon.arrow.up::before {
  content: "\e914";
}

i.icon.arrow.right {
  margin-right: 0;
}

i.icon.arrow.down {
  transform: rotate(90deg);
  font-weight: bold;
}

i.icon.arrow.down::before {
  content: "\e915";
}

i.icon.arrow.left {
  transform: rotate(180deg);
  margin-top: 1em;
}

i.icon.sort::before {
  content: "\e903";
}

i.icon.down-up-flip {
  width: 25px;
  height: 25px;
  margin: 0;
}

i.icon.down-up-flip::before, i.icon.down-up-flip::after {
  content: "";
  height: 2px;
  width: 11px;
  background: #697582 !important;
  position: absolute;
  right: 4px;
  top: 10px;
  border-radius: 2px;
  transition: all 85ms cubic-bezier(0.2, 0.2, 0.38, 0.9) 0s;
}

i.icon.down-up-flip::before {
  transform: rotate(45deg);
  right: 11px;
}

i.icon.down-up-flip::after {
  transform: rotate(-45deg);
}

i.icon.add::before {
  content: "\e902";
  margin-left: 1px;
}

i.icon.duplicate::before {
  content: "\e90a";
}

i.icon.delete::before {
  content: "\e906";
}

i.icon.download::before {
  content: "\e909";
}

i.icon.upload::before {
  content: "\e911";
}

i.icon.logout::before {
  content: "\e90f";
}

i.icon.close::before {
  content: "\e910";
}

i.icon.document::before {
  content: "\e908";
  margin-left: 1px;
}

i.icon.edit::before {
  content: "\e90b";
}

i.icon.login::before {
  content: "\e90e";
}

i.icon.listview::before {
  content: "\e90d";
}

i.icon.daigram::before {
  content: "\e907";
}

i.icon.info::before {
  content: "\e90c";
}

.open > .icon.down-up-flip::before, .open > label > .icon.down-up-flip::before {
  transform: rotate(-45deg);
}

.open > .icon.down-up-flip::after, .open > label > .icon.down-up-flip::after {
  transform: rotate(45deg);
}

.icon-tile {
  text-align: center;
  display: inline-block;
}

.icon-tile .icon {
  margin: 0 auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 20px;
  color: #697582;
  padding-top: 5px;
}

.icon-tile .name {
  display: block;
  margin-top: 10px;
  color: #1C2026;
  font-size: 13px;
}

.navigation {
  font-family: "Roboto-Regular";
  background-image: linear-gradient(#000, #252a33);
  font-size: 15px;
  letter-spacing: 1px;
  width: 244px;
}

.navigation .brand {
  display: block;
  height: 78px;
  margin-bottom: 102px;
  padding: 26px 0 0 26px;
}

.navigation .brand:hover, .navigation .brand:focus {
  text-decoration: none;
}

.navigation .brand img {
  width: 175px;
  height: 52px;
}

.navigation .nav-list {
  list-style-type: none;
  padding: 0;
  height: calc(100vh - 180px);
  overflow: auto;
}

.navigation .nav-list .nav-item {
  height: 54px;
  overflow: hidden;
  width: 100%;
  transition: height 0.25s;
  border-left: 6px solid transparent;
  border-top: 1px solid rgba(56, 63, 73, 0.5);
  position: relative;
}

.navigation .nav-list .nav-item .nav-link {
  display: block;
  line-height: 52px;
  color: #c0c8d0;
  height: 100%;
  padding-left: 24px;
  cursor: pointer;
  transition: color .15s linear;
  /*&::after {
          font-family: "icomoon";
          content: "\e902";
          float: right;
          font-size: 14px;
          margin-right: 20px;
          color: #697582;
        }*/
}

.navigation .nav-list .nav-item .nav-link:hover, .navigation .nav-list .nav-item .nav-link:focus {
  color: #f1f1f1;
  text-decoration: none;
  transition: color .15s linear;
}

.navigation .nav-list .nav-item .nav-link::before, .navigation .nav-list .nav-item .nav-link::after {
  content: "";
  height: 2px;
  width: 9px;
  background-color: #697582;
  position: absolute;
  right: 26px;
  top: 28px;
  border-radius: 2px;
  transition: background-color 85ms cubic-bezier(0.2, 0.2, 0.38, 0.9) 0s;
}

.navigation .nav-list .nav-item .nav-link::before {
  transform: rotate(45deg);
  top: 23px;
}

.navigation .nav-list .nav-item .nav-link::after {
  transform: rotate(-45deg);
}

.navigation .nav-list .nav-item:last-child {
  border-bottom: 1px solid rgba(56, 63, 73, 0.5);
}

.navigation .nav-list .nav-item.active {
  border-left: 6px solid #00adef;
  border-top: none;
  transition: border-left .15s linear;
  /*.nav-link::after {
            color: #00adef; 
        }*/
}

.navigation .nav-list .nav-item.active .nav-link {
  border-top: 1px solid rgba(56, 63, 73, 0.5);
}

.navigation .nav-list .nav-item.active .nav-link::before, .navigation .nav-list .nav-item.active .nav-link::after {
  background-color: #00adef;
}

.navigation .nav-list .nav-item.active .nav-link::before {
  top: 24px;
}

.navigation .nav-list .nav-item.active .nav-link::after {
  top: 29px;
}

.navigation .nav-list .nav-item.has-sub-nav {
  border-left: none;
}

.navigation .nav-list .nav-item.has-sub-nav > .nav-link {
  padding-left: 30px;
}

.navigation .nav-list .nav-item.has-sub-nav > .nav-link::before, .navigation .nav-list .nav-item.has-sub-nav > .nav-link::after {
  transition: all 85ms cubic-bezier(0.2, 0.2, 0.38, 0.9) 0s;
}

.navigation .nav-list .nav-item.has-sub-nav.opened {
  height: auto;
  border-bottom: none;
}

.navigation .nav-list .nav-item.has-sub-nav.opened .sub-nav-list {
  display: block;
  list-style-type: none;
}

.navigation .nav-list .nav-item.has-sub-nav.opened > .nav-link {
  height: 52px;
  transition: all 0.5s;
  /*&::after {
              transform: rotate(-90deg);
              transition: 0.5s;
            }*/
}

.navigation .nav-list .nav-item.has-sub-nav.opened > .nav-link::before {
  transform: rotate(-45deg);
}

.navigation .nav-list .nav-item.has-sub-nav.opened > .nav-link::after {
  transform: rotate(45deg);
}

.navigation .nav-list .nav-item.has-sub-nav > .nav-link {
  /*&::after {
            transform: rotate(90deg);
            transition: 0.25s;
          }*/
}

.navigation .nav-list .nav-item.has-sub-nav > .nav-link::before {
  right: 30px;
  top: 26px;
}

.navigation .nav-list .nav-item.has-sub-nav > .nav-link::after {
  top: 26px;
  right: 25px;
}

.navigation .nav-list .nav-item .sub-nav-list {
  display: none;
  padding: 0;
  background-color: #242932;
}

.navigation .nav-list .nav-item .sub-nav-list .nav-item:last-child {
  border-bottom: none;
}

.navigation .nav-list .nav-item .sub-nav-list .nav-item .nav-link {
  padding-left: 44px;
}

.nav-header {
  padding: 26px;
  height: 102px;
  background-color: #000;
}

.nav-header .app-info {
  text-align: right;
}

.nav-header .app-info .app-name {
  font-family: "Roboto-Medium";
  font-size: 14px;
  letter-spacing: 2.69px;
  color: #1c2026;
  display: inline-block;
}

.nav-header .app-info .app-logo {
  margin-right: 30px;
  margin-left: 10px;
}

.btn {
  display: inline-block;
  font-family: "Roboto-Regular";
  font-weight: normal;
  color: #ffffff;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  background-color: #1c2026;
  border: 1px solid transparent;
  padding: 0px 35px;
  height: 40px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 0.15s linear;
}

.btn.big {
  height: 50px;
  padding: 10px 40px;
}

.btn.btn-secondary {
  background-color: transparent;
  color: #1c2026;
  border: 1px solid #1c2026;
}

.btn.btn-secondary:hover {
  background-color: transparent;
  color: #76797c;
  border: 1px solid #76797c;
}

.btn.btn-secondary.disabled, .btn.btn-secondary:disabled {
  background-color: transparent;
  color: #99a5b3;
  border: 1px solid #d5d9de;
}

.btn.btn-text {
  background-color: transparent;
  color: #1c2026;
  padding: 0;
}

.btn.btn-text:hover {
  background-color: transparent;
  color: #76797c;
}

.btn.btn-text.disabled, .btn.btn-text:disabled {
  background-color: transparent;
  color: #99a5b3;
}

.btn.arrow {
  padding: 0;
  width: 40px;
}

.btn.arrow.big {
  width: 50px;
}

.btn.arrow.big::before {
  content: "\e900";
  font-size: 20px;
  line-height: 49px;
  padding-left: 1px;
}

.btn.arrow.btn-text {
  width: auto;
}

.btn.arrow.btn-text::before {
  float: right;
  font-size: 18px;
  padding-left: 16px;
  line-height: 24px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 0.15s linear;
}

.btn.arrow.btn-text:hover:not(:disabled)::before {
  color: #76797c;
  transform: translateX(5px);
}

.btn.arrow.btn-text.back::before {
  content: "\e914";
  float: left;
  padding-right: 16px;
  padding-left: 0;
  line-height: 23px;
}

.btn.arrow.btn-text.back:hover:not(:disabled)::before {
  transform: translateX(-5px);
}

.btn.arrow::before {
  font-family: "icomoon";
  content: "\e915";
  font-size: 15px;
  line-height: 39px;
}

.btn:hover {
  background-color: #3d4146;
  text-decoration: none;
}

.btn:focus, .btn.focus {
  outline: 0;
}

.btn.disabled, .btn:disabled {
  color: #99a5b3;
  background-color: #d5d9de;
}

.btn.btn-icon-circle {
  padding: 0;
  background-color: transparent;
  color: #00adef;
  border: none;
  border-radius: 50%;
}

.btn.btn-icon-circle:hover {
  background-color: rgba(0, 173, 239, 0.08);
}

.btn-set .btn {
  margin-right: 16px;
  margin-top: 16px;
}

.btn-set .btn:last-child {
  margin-right: 0;
}

