.expansion-panel-group {
  border-bottom: 1px solid #c0c8d0;
}

.expansion-panel-group .expansion-panel {
  font-family: "Roboto-Regular";
  font-size: 14px;
  width: 100%;
  overflow: hidden;
  border-top: 1px solid #c0c8d0;
  position: relative;
  color: #00ADEF;
}

.expansion-panel-group .expansion-panel.open {
  color: #697582;
}

.expansion-panel-group .expansion-panel .animation-wrapper {
  position: absolute;
  width: 38px;
  height: 38px;
  right: 4px;
  top: 5px;
  border-radius: 50%;
}

.expansion-panel-group .expansion-panel-label {
  display: flex;
  justify-content: space-between;
  padding: 14px 44px 13px 32px;
  cursor: pointer;
  margin: 0;
  color: #383F49;
  position: relative;
  /* Icon */
}

.expansion-panel-group .expansion-panel-label:hover {
  color: black;
}

.expansion-panel-group .expansion-panel-label i.icon {
  position: absolute;
  right: 10px;
  /*float: right;
        font-size: 15px;
        margin-right: -33px;
        color: #697582;
        transition: all 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;*/
}

.expansion-panel-group .expansion-panel-content {
  max-height: 0;
  opacity: 0;
  transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1) 0s;
  overflow: hidden;
  color: #1c2026;
}

.expansion-panel-group .expansion-panel-content > p {
  padding: 10px 32px 25px;
  margin: 0;
  white-space: pre-wrap;
}

.expansion-panel-group .expansion-panel-close {
  display: flex;
  justify-content: flex-end;
  padding: 1em;
  font-size: 0.75em;
  cursor: pointer;
}

.expansion-panel-group .expansion-panel-close:hover {
  color: #697582;
}

.expansion-panel-group .expansion-panel input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.expansion-panel-group .expansion-panel.disabled {
  pointer-events: none;
}

.expansion-panel-group .expansion-panel.disabled .expansion-panel-label {
  color: #99a5b3;
}

.expansion-panel-group .expansion-panel.disabled .expansion-panel-label i.icon::before, .expansion-panel-group .expansion-panel.disabled .expansion-panel-label i.icon::after {
  background: #C0C8D0 !important;
}

.expansion-panel-group .expansion-panel input:checked + .expansion-panel-label > i.icon {
  /*transform: rotate(-90deg);
          transition: all 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
          margin-right: -28px;
