.progress {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 5px solid rgba(217, 223, 228, 0.7);
  border-radius: 50%;
}

.progress.infinite {
  border-top-color: #00ADEF;
  animation: spin 1s linear infinite;
  -webkit-animation: spin 1s linear infinite;
}

.progress.determinite {
  position: relative;
}

.progress.determinite .progress-ring {
  position: absolute;
  top: -5px;
  left: -5px;
  width: 50px;
  height: 50px;
  transform: rotate(-90deg);
}

.progress.determinite .progress-ring .progress-ring-circle {
  stroke: #00ADEF;
  stroke-width: 5;
  fill: transparent;
  transition: stroke-dashoffset 0.35s;
}

.progress-block-wrapper {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 9999;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

